
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: "Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #42bd56;
}
#app {
  width: 10rem;
  margin: 0 auto;
}
.page {
  padding-top: 1.253333rem;
  overflow-x: hidden;
}
section {
  overflow: hidden;
}
section header {
  padding: 0 0.533333rem;
  padding-top: 0.266667rem;
  background-color: #fff;
}
section h2 {
  display: inline-block;
  margin: 0 0 0.2rem;
  font-size: 0.453333rem;
  font-weight: normal;
}
section header a {
  float: right;
  font-size: 0.373333rem;
  color: #42bd56;
}
section .section-content {
  margin-bottom: -0.533333rem;
}
.item {
  display: inline-block;
  vertical-align: top;
  width: 2.666667rem;
  text-align: center;
  margin-left: 0.24rem;
}
.item:first-child {
  margin-left: 0.533333rem;
}
.item> * {
  font-size: 0.373333rem;
  white-space: normal;
}
.row {
  padding: 0.4rem 0 1.146667rem 0;
}
.items {
  font-size: 0;
  white-space: nowrap;
  overflow: hidden;
  overflow-x: auto;
}
.items::-webkit-scrollbar {
  display: none;
}
.interests ul {
  white-space: nowrap;
  overflow-x: auto;
  padding: 0.4rem 0.4rem 1.146667rem 0.4rem;
  font-size: 0;
  margin-left: -0.133333rem;
}


